.adviceContainer{
    background-color: var(--DarkGrayishBlue);
    max-width: 450px;
    height: auto;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-around;
    padding: 3%;
}

.adviceNumber{
    color: var(--NeonGreen);
    text-transform: uppercase;
    letter-spacing: 3px;
    margin-bottom: 30px;
}
.adviceText{
    font-size: 28px;
    text-align: center;
    color: var(--LightCyan);
    margin-bottom: 30px;
}

.iconDivider{
    margin-bottom: 50px;
}

.iconDiceContainer{
    background-color: var(--NeonGreen);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: relative;
    top:-8%;
    left: 45%;
}

.iconDice{
    padding: 10px;
}

.iconDiceContainer:hover{
    box-shadow: 0 0 40px var(--NeonGreen);
}

@media screen and (max-width: 380px) {
    .iconDiceContainer{
        left: 42%;
    }
}